import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { QnaComponent } from './forum/qna/qna.component';
import { QnaDetailComponent } from './forum/qna/qna-detail/qna-detail.component';
import { QnaWriteComponent } from './forum/qna/qna-write/qna-write.component';
import { ForumComponent } from './forum/forum.component';
import { GuideComponent } from './forum/guide/guide.component';
import { GuideDetailComponent } from './forum/guide/guide-detail/guide-detail.component';
import { GuideWriteComponent } from './forum/guide/guide-write/guide-write.component';

const routes: Routes = [
  {
    path: 'home',
    component: HomeComponent
  },
  {
    path: 'forum',
    component: ForumComponent
  },
  {
    path: 'forum/qna',
    component: QnaComponent
  },
  {
    path: 'forum/qna/detail/:id',
    component: QnaDetailComponent
  },
  {
    path: 'forum/qna/write',
    component: QnaWriteComponent
  },
  {
    path: 'forum/guide',
    component: GuideComponent
  },
  {
    path: 'forum/guide/detail/:id',
    component: GuideDetailComponent
  },
  {
    path: 'forum/guide/write',
    component: GuideWriteComponent
  },
  { path: '', redirectTo: 'home', pathMatch: 'full' },
  { path: '**', redirectTo: 'home' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
