import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';   
import { ForumService } from '../../service/forum.service';

@Component({
  selector: 'app-qna-detail',
  templateUrl: './qna-detail.component.html',
  styleUrls: ['./qna-detail.component.css']
})
export class QnaDetailComponent implements OnInit {

  topic_id:number=0;
  topic_data:any={};
  topic_user:any={};
  topic_answers:Array<any>=[];
  topic_attach_file:Array<any>=[];
  answer_attach_file:Array<any>=[];

  current_user:any={};
  
  constructor(private route: ActivatedRoute, private _forumService:ForumService) {
    this.route.params.subscribe(res => 
      this.topic_id=res.id
    );
    
  }

  ngOnInit() {
    this._forumService.getTopicDetail(this.topic_id).subscribe(data =>{
      console.log(data);
      this.topic_data=data;
      this.topic_user=data['user'];
      this.topic_answers=data['answers'];
      this.topic_attach_file=data['attachFile'];
      
    }
    ,
    (err)=>{
        console.log(err,err.message);
      }
    );
    
    this._forumService.getUserSession().subscribe(data =>{
      console.log(data);
      this.current_user=data['user'];
     
    }
    ,
    (err)=>{
        console.log(err,err.message);
      }
    );


  }

  downloadFile(attach_file) {
    console.log('downloadFile:'+attach_file.id);

    this._forumService.downloadByFileIdUrl(attach_file.id).subscribe(data =>{
      console.log(data);
      
      window.URL.createObjectURL(data);

      var url = window.URL.createObjectURL(data);
      var a = document.createElement('a');
      document.body.appendChild(a);
      a.setAttribute('style', 'display: none');
      a.href = url;
      a.download = attach_file.fileName;
      a.click();
      window.URL.revokeObjectURL(url);
      a.remove(); // remove the element
    }
    ,
    (err)=>{
        console.log(err,err.message);
      }
    );

  }

}
