import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { CKEditorComponent } from 'ng2-ckeditor';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';

import { Errors } from '../../../common/models/errors.model';

import * as urls from "../../../common/urls";

@Component({
  selector: 'app-qna-write',
  templateUrl: './qna-write.component.html',
  styleUrls: ['./qna-write.component.css']
})
export class QnaWriteComponent implements OnInit {

  title: String = '';
  registerForm: FormGroup;
  submitted = false;

  errors: Errors = {errors: {}};

  ckEditorConfig: any;
  topicName: string = 'Q&A';

  @ViewChild( CKEditorComponent) ckEditor: CKEditorComponent;
  
  constructor(private formBuilder: FormBuilder,private cd: ChangeDetectorRef,
    private _http: HttpClient) { 
      this.registerForm = this.formBuilder.group({
        title: ['', Validators.required],
        content: ['', Validators.required],
        file: [''],
      });
    
  }

  ngOnInit() {
    let editor = this.ckEditor.instance;

    console.log('qna-write');
  }

  // convenience getter for easy access to form fields
  get f() { return this.registerForm.controls; }


  onFileChange(files: FileList) {
    const reader = new FileReader();
 
    console.log('onFileChange:'+event);

    if (files && files.length > 0) {
      
      // For Preview
      const file = files[0];
      
      reader.readAsDataURL(file);
  
      reader.onload = () => {
        this.registerForm.patchValue({
          file: reader.result
       });
      
        // need to run CD since file load runs outside of zone
        this.cd.markForCheck();
      };
    }
  }


  onSubmit(files: FileList) {
    this.submitted = true;

    // stop here if form is invalid
    if (this.registerForm.invalid) {
        return;
    }

    //alert('SUCCESS!! :-)\n\n' + JSON.stringify(this.registerForm.value))
    
    const formData = new FormData();

    console.log(this.registerForm);
    console.log(files);

    const formValue = this.registerForm.value;

    if(files[0]){
      formData.append('file', files[0]);
    }
    formData.append('title', formValue.title);
    formData.append('content', formValue.content);
    formData.append('category', 'qna');

    this._http.post(urls.topicsUrl, formData)
      .subscribe(res => {
        
        console.log(res);
    });
    
    


  }


}
