import { Component, OnInit, ViewChild, ChangeDetectorRef, AfterViewChecked } from '@angular/core';
import { CKEditorComponent } from 'ng2-ckeditor';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';

import { Errors } from '../../../common/models/errors.model';

import * as urls from "../../../common/urls";
import { GuideTemplateService } from '../../service/guide-template.service';
import { ForumService } from '../../service/forum.service';



@Component({
  selector: 'app-guide-write',
  templateUrl: './guide-write.component.html',
  styleUrls: ['./guide-write.component.css']
})
export class GuideWriteComponent implements OnInit, AfterViewChecked {

  
  title: String = '';
  registerForm: FormGroup;
  submitted = false;

  errors: Errors = {errors: {}};

  ckEditorConfig: any;
  topicName: string = '가이드';
  topicDesc: string = '';


  selectedTemplate: string = '';

  itemList:any = [];
  selectedItems = [];
  settings = {};
  

  @ViewChild( CKEditorComponent) ckEditor: CKEditorComponent;
  
  constructor(private formBuilder: FormBuilder,
    private cd: ChangeDetectorRef,
    private _http: HttpClient,
    private guideTemplateService:GuideTemplateService,
    private forumService:ForumService
  ) { 
      this.registerForm = this.formBuilder.group({
        title: ['', Validators.required],
        content: ['', Validators.required],
        file: [''],
        selectedItems: [[]]
      });
    
      this.forumService.getGuideSpectator().subscribe(data =>{
        this.itemList = JSON.parse(JSON.stringify(data));

        console.log(this.itemList);
      }
      ,
      (err)=>{
          console.log(err,err.message);
        }
      );
      
      this.settings = {
        singleSelection: false, 
        text:"관람자를 선택하세요.",
        selectAllText:'전체 선택',
        unSelectAllText:'전체 해제',
        enableSearchFilter: true,
        enableFilterSelectAll: false,
        groupBy:"category"
      };

  }

  ngAfterViewChecked() {
    let editor = this.ckEditor.instance;
    // ckeditor
    editor.config.height=400;
    editor.config.filebrowserUploadUrl  = urls.uploadImageFileIdUrl;
  }

  ngOnInit() {
    // multi drop list
    this.topicDesc = this.guideTemplateService.getRestfulTemplate();
    this.registerForm.get('content').setValue(this.topicDesc);

  }

  // 첨부파일
  get f() { return this.registerForm.controls; }

  onFileChange(files: FileList) {
    const reader = new FileReader();
 
    console.log('onFileChange:'+event);

    if (files && files.length > 0) {
      
      // For Preview
      const file = files[0];
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.registerForm.patchValue({
          file: reader.result
       });
        // need to run CD since file load runs outside of zone
        this.cd.markForCheck();
      };
    }
  }

  // form summit
  onSubmit(files: FileList) {
    this.submitted = true;

    // stop here if form is invalid
    if (this.registerForm.invalid) {
        return;
    }

    //alert('SUCCESS!! :-)\n\n' + JSON.stringify(this.registerForm.value))
    const formData = new FormData();

    console.log(this.registerForm);
    console.log(files);

    const formValue = this.registerForm.value;

    if(files[0]){
      formData.append('file', files[0]);
    }
    formData.append('title', formValue.title);
    formData.append('content', formValue.content);
    formData.append('category', 'guide');
    
    var permitMember:string='';
    
    for(let item in formValue.selectedItems){
      if(item == "0"){
        permitMember = formValue.selectedItems[item].id;
        continue;
      }
      permitMember = permitMember+','+formValue.selectedItems[item].id;
    } 

    console.log(permitMember);

    formData.append('permit',permitMember);

    this._http.post(urls.topicsUrl, formData)
      .subscribe(res => {
        
        console.log(res);
    });
    
  }

  //event handler for the select element's change event
  selectChangeTemplateHandler (event: any) {
    //update the ui
    this.selectedTemplate = event.target.value;

    switch(this.selectedTemplate) {
      case "restful" : {
        
        this.topicDesc = this.guideTemplateService.getRestfulTemplate();
        this.registerForm.get('content').setValue(this.topicDesc);
        break;
      }
      case "tcp" : {
        this.topicDesc = this.guideTemplateService.getTcpTemplate();
        this.registerForm.get('content').setValue(this.topicDesc);
        break;
      }
      case "etc" : {
        this.topicDesc = this.guideTemplateService.getEtcTemplate();
        this.registerForm.get('content').setValue(this.topicDesc);
        break;
      }
    }

  }

  // multi drop list
  onItemSelect(item: any) {
    console.log(item);
    console.log(this.registerForm.get('selectedItems').value);
  }
  OnItemDeSelect(item: any) {
      console.log(item);
      console.log(this.selectedItems);
  }
  onSelectAll(items: any) {
      console.log(items);
  }
  onDeSelectAll(items: any) {
      console.log(items);
  }
  


  public refreshDemo1Values(value: any): void {
   console.log(value);
  }

}
