import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import * as urls from "../../common/urls";

@Injectable({
  providedIn: 'root'
})
export class ForumService {

  constructor(private _http: HttpClient) { }
  
  getTopicList(page:number) {
    return this._http.get(urls.topicsUrl+'?page='+page);
  }

  getTopicDetail(topicId:number) {
    return this._http.get(urls.topicsUrl+'/'+topicId);
  }

  getQnaList(page:number) {
    return this._http.get(urls.qnaUrl+'?page='+page);
  }

  getGuideList(page:number) {
    return this._http.get(urls.guideUrl+'?page='+page);
  }

  getGuideSpectator() {
    console.log(urls.spectatorUrl);
    
    return this._http.get(urls.spectatorUrl);
  }

  getUserSession() {
    return this._http.get(urls.userBySessionUrl);
  }

  downloadByFileIdUrl(id:number) {
    return this._http.get(urls.downloadByFileIdUrl+'/'+id,{ responseType: 'blob' });
  }

}
